/*

### Component ###
This component contains the modal dialog for playing multiplayers

## Props ##

## Behavior ##

*/

<template>
  <!-- Modal dialog pour afficher les mots -->
  <b-modal
    id="modalplaymultiplayers"
    ref="modalplaymultiplayers"
    title="Jeu multijoueurs"
    :hide-footer="true"
  >
    <b-card
      title="Multi-joueurs temps rel"
      :img-src="require('@/assets/images/play-multiplayers.jpg')"
      img-alt="Jeu multijoueurs"
      img-top
      style=""
      class="shadow-sm mb-3 bt-white rounded"
    >
      <b-card-text class="text-left">
        Jouez en temps rel sur le mme ordinateur / tlphone / tablette ou chacun le vtre. <br>
        Piochez un mot et faites-le deviner aux autres grce aux images <i>Concept</i>.<br>
        Deux modes de jeu sont possibles :
        <ul>
          <li>Vous pouvez jouer sur le mme ordinateur / tablette / smartphone mais <strong> attention, pensez bien  masquer l'cran lorsque vous lancez le jeu car les mots vont apparatre  l'cran</strong>. Puis faites deviner aux autres joueurs une fois que vous connaissez le mot et que vous faites deviner.</li>
          <li>Chacun sur son ordinateur / tablette / smartphone en crant ou en rejoignant une partie existante.</li>
        </ul>
      </b-card-text>
      <b-button v-b-modal.words @click="launchPlaySameDevice()" variant="primary" class="m-1">Sur le mme ordi <font-awesome-icon icon="clock" /></b-button>
      <b-button @click="launchMultiplayers()" variant="primary" class="m-1">Crer une partie <font-awesome-icon icon="puzzle-piece" /></b-button>
    </b-card>
  </b-modal>
</template>

<script>
import { EventBus } from '@/event-bus.js'
import { mapState, mapMutations, mapActions } from 'vuex'

export default {
  name: 'ModalPlayMultiplayers',
  components: { },
  props: { },
  computed: mapState ({
    alerts: state => state.alerts.alerts,
    user: state => state.user.user
  }),
  // created () {
  //   EventBus.$on('show_modal_playmultiplayers', data => this.showModal())
  // },
  methods: {
    ...mapMutations([
      'setPageAfterLogin'
    ]),
    ...mapActions([
      'setGameMode'
    ]),
    hideModal: function () {
      this.$refs.modalplaymultiplayers.hide()
    },
    // showModal() {
    //   this.$refs.modalplaymultiplayers.show()
    // },
    createBoard: function() {
      this.hideModal()
      this.setGameMode('boardCreation')
      EventBus.$emit('show-modal-words', {})
    },
    displayBoardList: function() {
      this.hideModal()
      this.$router.push({ name: 'BoardsTable' })
    },
    launchMultiplayers: function() {
      this.hideModal()
      // show the main board paage
      this.$router.push({ name: 'Mainboard' })
      // if user not authenticated, force auth first else show multiplayers modal
      if (!this.user) {
        this.setPageAfterLogin('modalmultiplayers')
        this.$root.$emit('bv::show::modal', 'modallogin')
      } else {
        this.$root.$emit('bv::show::modal', 'modalmultiplayers')
      }
    },
    launchPlaySameDevice() {
      // display the main board and the word modal
      this.$router.push({ name: 'Mainboard' })
      this.$root.$emit('bv::show::modal', 'modalwords')
      this.setGameMode('local')
      this.hideModal()
    }
  }
}
</script>

<style scoped >
.card {
  transition: box-shadow .3s;
}
.card:hover {
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.175) !important;
}
</style>



